.CreateTodoButton{
	background-color: #3B7ED1;
	box-shadow: 0px 5px 25px rgba(97,218,250,0.5);
	border: none;
	border-radius: 50%;
	cursor: pointer;
	font-size: 50px;
	position: fixed;
	bottom: 24px;
	right: 24px;
	font-weight: bold;
	color: #0D2E57;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 64px;
	width: 64px;
	
	transform: rotate(0);
	transition: .3s ease;
	z-index: 1;

}

.CreateTodoButton:hover{
	transform: rotate(224deg);	
}